/* Questionnaire */

.questionnaire {
  background-color: #eeebfe;
  margin: 0;
  background: linear-gradient(
      217deg,
      rgba(99, 113, 182, 0.8),
      rgba(99, 113, 182, 0) 70.71%
    ),
    linear-gradient(
      127deg,
      rgba(90, 199, 218, 0.8),
      rgba(90, 199, 218, 0) 70.71%
    ),
    linear-gradient(
      27deg,
      rgba(243, 243, 243, 0.8),
      rgba(243, 243, 243, 0) 70.71%
    ),
    linear-gradient(
      336deg,
      rgba(240, 98, 146, 0.8),
      rgba(240, 98, 146, 0) 70.71%
    );
  background-size: contain;
  background-repeat: repeat;
  height: 100vh;
}

.questionnaire__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  // align-items: center;
  .submit{
    display: flex;
    justify-content: flex-end;
    width: 100%;
    margin-top: 4*$spacing;
    .btn{
      // margin: 0;
      &.btn--hidden{
        display: none;
      }
    }
  }
}

.questionnaire__description{
  // margin-top: 3*$spacing;
  margin-bottom: 20px;
  color: $brand-teal;
  font-family: 'Asap', sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 24px;
}

.questionnaire__explanation{
  margin-top: 40px;
}

.question__block {
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  padding: 24px 40px;
  border-radius: 16px;
  margin-bottom: 40px;
  width: 880px;
  box-shadow: 0 8px 6px -6px rgba(0, 0, 0, 0.15);
  margin-top: 2*$spacing;
  transition:  height 2s;
  box-sizing: content-box;
  .loading{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .results{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    @include md {
      flex-direction: row;
    }
    &__copy{
      width:100%;
      @include md {
        width: 75%;
      }
    }
    &__icon{
      width:143px;
      height:auto;
    }
  }
  .question__list{
    padding: 0;
    margin: 0;
    width: auto;
    @include md {
      width: 880px;
    }
    // height: 120px;
    .question {
      display: none;
      opacity: 0;
      height: 0;
      // position: absolute;
      pointer-events: none;
      flex-direction: column;
      margin-left: -1*$spacing;

      .questionnaire__buttons{
        //display:flex;
        justify-content:center;
        margin-bottom: 20px;
        a{
          margin:0 4px;
          font-size:14px;
          padding: $spacing (2*$spacing)+4;
          margin-bottom: 20px;
          width: auto; /* 80px */
          height: 36px;
          vertical-align: middle;
          color: #4E4B6D;
          display: inline-flex;
          justify-content: center;
          align-items: center;
          box-sizing: initial;
          text-align: center;

          &:hover {
            color: #FFFFFF;
          }
        }

        .btn-secondary--outline {
          border-color: #F0F0F0;
          background-color: #F0F0F0;

          &.btn--selected, &:hover {
            // border-color: #0FA5B1;
            // background-color: #0FA5B1;
            border-color: $brand-teal;
            background-color: $brand-teal;
            color: #FFFFFF;
          }
        }
      }

    }
    .question--active {
      display:flex;
      opacity: 1;
      flex-direction: column;
      height: auto;
      pointer-events: auto;
      margin-left: 0*$spacing;
      transition: opacity 0.3s ease-in-out 0.1s, margin 0.3s ease-out;
    }
    p {
      margin: 0;
      padding: 0;
    }
    h2 {
      margin: 0 0 40px 0;
      padding: 0;
      font-size: 24px;
    }
    .hidden {
      display: none;
    }
  }
}


/* Questionnaire navigation*/

.questionnaire__nav {
  display: flex;
  width: 100%;
  justify-content: space-between;

  .questionnaire__progress {
    //margin: auto 16px;
    height: 8px; /* Can be anything */
    position: relative;
    background: #d8d8d8;
    -moz-border-radius: 4px;
    -webkit-border-radius: 4px;
    border-radius: 4px;
    padding: 0px;
    width: 100%;
    // box-shadow: inset 0 -1px 1px rgba(255,255,255,0.3);
    & > span {
      display: block;
      height: 100%;
      border-top-right-radius: 8px;
      border-bottom-right-radius: 8px;
      border-top-left-radius: 20px;
      border-bottom-left-radius: 20px;
      // background-color: #5ac7da;
      background-color: $brand-teal; 
      position: relative;
      overflow: hidden;
      transition: .5s width ease-in-out;
      width: 0%;
    }
  }

  .questionnaire__pagination {
    margin-top: 45px;
  }

  .questionnaire__navCircle {
    border-radius: 50%;
    min-width: 42px;
    min-height: 42px;
    border: 2px solid #6371b6;
    position: relative;
    background-color: transparent;
    cursor: pointer;
    margin-top: 30px;

    &:hover{
      background-color: #97a0ce;
      color: white;
      outline-color: #97a0ce;
      border: 2px solid #97a0ce;
      > .questionnaire__navButtons{
        border: 3px solid #FFFFFF;
        border-left: 0;
        border-bottom: 0;
      }
    }

    .questionnaire__navButtons {
      border: 3px solid #6371b6;
      border-left: 0;
      border-bottom: 0;
      width: 10px;
      height: 10px;
      margin: 0 auto;
      position: absolute;
      top: 50%;
      left: 50%;
      border-radius: 2px;
    }

    .questionnaire__navButtons--prev--text {
      position: absolute;
      left: 55px;
      top: 12px;
      color:#4E4B6D;
      cursor: pointer;
    }

    .questionnaire__navButtons--next--text {
      position: absolute;
      right: 55px;
      top: 12px;
      color:#4E4B6D;
      cursor: pointer;
    }

    &.questionnaire__navButtons--disabled{
      border: 2px solid #d8d8d8;
      cursor: auto;
      &:hover{
        border: 2px solid #d8d8d8;
        background-color: transparent;

      }
      .questionnaire__navButtons {
        border: 3px solid #d8d8d8;
        border-left: 0;
        border-bottom: 0;
      }

      .questionnaire__navButtons--prev--text, .questionnaire__navButtons--next--text {
        color:#d8d8d8;
        cursor: default;
      }
    }

    &.questionnaire__navButtons--prev .questionnaire__navButtons{
      transform: translate(-30%, -50%) rotate(-135deg);
    }

    &.questionnaire__navButtons--next .questionnaire__navButtons{
      transform: translate(-70%, -50%) rotate(45deg);
    }

  }
}

/* Responsive Style */
@media only screen and (max-width: 1023px) {
  .questionnaire__description{
    // margin: 0 30px;
    // margin-top: $spacing;
    margin-bottom: 20px;
  }

  .question__block {
    padding: 0;
    margin-bottom: 0;
    width: 100%;
    border-radius: 0;
    box-shadow: none;

    .question__list {
      .question {
        .questionnaire__buttons {
          a {
            display: flex;
            width: auto;
          }
        }
      }
    }
  }

  .questionnaire__pagination__text {
    display: none;
  }

  #QuestionnaireTakeQuestionnaireForm {
    margin: 0 30px 30px 30px;
  }
  .nav-login.startFreeCourse a{
    margin:auto;
  }
}
