.watch-video > span {
  display: inline-block;
    vertical-align: middle;
    position: relative;
    width: 26px;
    height: 26px;
    background: #c9467f;
    border-radius: 50%;
    transform: rotate(90deg);
    margin-left: 5px;
    margin-bottom: 3px;

    &::after {
      position: absolute;
    content: "";
    top: 52%;
    left: 50%;
    border: 7px solid transparent;
    border-bottom: 12px solid #f4f8ff;
    transform: translateX(-50%) translateY(-85%);
    border-radius: 2px;
    }
}

