.grid2x2 {
  min-height: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;

  & > div {
    display: flex;
    justify-content: center;
    flex-direction: column;
    border-radius: 5px;
    margin-bottom: 24px;
    margin: 0 12px 24px;
    min-height: 200px;

    @include xs {
      width:100%;
      flex-grow: 1;
      flex-basis: 100%;

      // margin: 0;
    }

    @include sm {
      flex-grow: 1;
      flex-basis: 40%;

      // width:352px;
      // flex-basis: calc(50% - 40px);
      // margin: 0;
    }

    @include md {
      flex-grow: 1;
      flex-basis: 40%;

      // width:462px;
      // flex-basis: calc(50% - 40px);
    }

    @include lg {
      flex-grow: 1;
      flex-basis: 40%;

      // width:320px;
      // flex-basis: calc(50% - 40px);
    }

    .masonry-content {
      display: flex;
      flex-direction: column;
      justify-content: center;
      // align-items: center;
      text-align: center;
      // height: 100%;
      flex-grow: 1;

      a {
        align-self: center;
      }

      p {
        text-align: center;
      }

      .watch-video > span {
        @include watch-video-icon();
      }

      &--img {
        //module block image size control
        //change max height to adjust size, width will match max height
        max-height: 125px;
        // width: 100%;
      }

      &-image-container, &--img {
        flex-grow: 1;
        // height:100%;
        margin: auto;
      }

      .icons {
				display: flex;
				flex-direction: row;
				width: 100%;
        align-self: center;

        a {
          margin: auto;
        }

				@include sm {
					width: 80%;
				}

				a:hover {
					opacity: 0.5;
					-webkit-transition: opacity 0.3s ease;
					-o-transition: opacity 0.3s ease;
					transition: opacity 0.3s ease;
				}

        img {
					width: 50px;
					height: 50px;
					border-radius: 50%;
				}
			}
      @supports (justify-content: space-evenly){
        .icons {
          justify-content: space-around;
        }
      }

      @supports (justify-content: space-evenly){
        .icons{
          justify-content: space-evenly;
        }
      }

      &--align-left {
        align-items: flex-start;
        justify-content: space-evenly;

        h2 {
          text-align: start;
        }
        @media (max-width: $screen-xs-max) {
          align-items: center;
          h2 {
            text-align: center;
          }
        }
      }
    }
  }
}
