$blog-card-img-height: 200px;
$blog-card-content-height: 200px;

.button-back {
  @include button($white, $brand-pink, $brand-pink);

  &::before {
    @include back-tick-css-version($white);
  }
}

.wrapper.post, .wrapper.news {

  .blog-date-container { //helps position date icon and date text inline with eachother
    display: flex;
    align-items: center;
    padding-bottom: 8px;
  }


  .read-more {
    font-weight: 600;
    font-size: 15px;
  }

  .socials h2 {
    color: $white;
  }

}

.wrapper.post {
  .button-back {
    margin-top: 0;
    @include sm {
      margin-top: $spacing * 4;
    }
  }
  .gradient {
    @media (max-width: 767px) {
      background: $white;
    }
  }
}

.blog-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 18px;
  margin-bottom: 30px;
  margin-top: 20px;

  .masonry-sizer {
    margin: 12px 0;
    width: 100%;
    @include sm {
      width: calc(50% - 24px);
      margin: 12px;
    }
    @include md {
      width: calc(33% - 24px);
      margin: 12px;
    }
  }

  &::before {
    content: none;
  }

  .copy {
    max-height: 2.6rem;
    overflow: hidden;
    // padding-right: 1rem;
    // position: relative;
    // white-space: nowrap;
    // text-overflow: ellipsis;
  }
}
// end .blog-container

.blog-card {
  box-sizing: border-box;
  width: 48%;
  // height: calc($blog-card-img-height + $blog-card-content-height);
  &-content {
    padding: 10px 20px 30px;
    // border-right: 2px solid #eeefef;
    // border-left: 2px solid #eeefef;
    // border-bottom: 2px solid #eeefef;
    border-radius: 0 0 16px 16px;
    height: $blog-card-content-height;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .blog-card-title {
      color: #4e4b6d;
      text-decoration: none;

      &:hover {
        color: #4e4b6d;
        text-decoration: underline;
        cursor: pointer;
      }
    }
  }

  &-extras {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    & .post-date {
      font-size: $font-size-base;
      color: $gray-light;
    }
  }

  &-img {
    height: $blog-card-img-height;
    background-size: cover;
background-position: center;
border-radius: 16px 16px 0 0;
  }

  & img {
    border-radius: 0;
    overflow: hidden;
    width: 100%;

    @include sm {
      border-radius: 16px 16px 0 0;
    }
  }

  &.card {
    margin: 0 0 10px;
    padding: 0;
    border: none;

    &.masonry-item {
      margin: 12px 0;
      width: 100%;
      @include sm {
        width: calc(50% - 24px);
        margin: 12px;
      }
      @include md {
        width: calc(33% - 24px);
        margin: 12px;
      }
    }

    &.masonry-item--double {
      width: 100%;
      @include sm {
        width: calc(50% - 24px);
        margin: 12px;
      }
      @include md {
        width: calc(66% - 24px);
        margin: 12px;
      }
    }
  }
  // end .blog-card.card
}
// end .blog-card

.wrapper.news {

  .center-box > a {
    @include button($white, $brand-pink, $brand-pink);
  }

  .fill-bottom {
    padding-bottom: 80px;
  }

  .section-heading {
    text-align: center;

    & > p {
      text-align: inherit;
    }
  }

  .read-more{
    // font-size: $font-size-base;
  }


  .blog-card {
    &-img img {
      border-radius: 16px 16px 0 0;
    }
  }

  .blog-date {
    color: $terms-gray;
    font-size: $font-size-small;
    font-weight: 400;
    @include sm {
      // font-size: 15px;
    }
  }

  .date-icon {
    width: 15px;
    margin-right: 5px;
  }
}

.wrapper.post {

  .blog-card.card {
    @media (max-width: 767px) {
      background: transparent;
      border: none;
      box-shadow: none;
    }
  }

  .fill-bottom {
    padding-bottom: 80px;
  }

  .page .container {
    padding: 0;
    @include sm {
      padding: 15px;
    }
  }

  .page {
    padding-top: 0;
    @include sm {
      padding-top: 30px;
    }
  }

  .blog-card {
    width: 100%;
  }

  .blog-post {

    &-img {
      overflow: hidden;
      height: auto;
      @include sm {
        max-height: 524px; //container size 930px x 524px creates a 16:9 aspect ratio on bigger screens.
      }
    }

    .blog-date {
      color: $terms-gray;
      font-size: 14px;
      font-weight: $font-medium;
    }

    .date-icon {
      width: 15px;
      margin-right: 5px;
    }

    &-content {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      padding: 0 15px;
      @include sm {
        padding: 0;
        flex-direction: row;
        padding: 0 $blog-content-large-padding;
      }

      h1 {
        margin-bottom: 1em;
      }

      .article {
        margin: 0;
        width: 100%;
        padding-top: 16px;
        @include sm {
          margin-right: 60px;
          width: 80%;
        }

        blockquote {
          color: $black;
          text-align: left;
          border-left: 3px solid $brand-purple;
          padding-left: 14px;
          font-weight: $font-semi-bold;
          font-size: $font-size-large;
          margin: 1.5em 0;
        }
      }

      .socials {
        background: #6371b6;
        width: 100%;
        height: 100px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 12px 26px 0;
        color: white;
        float: right;
        border-radius: 16px;
        margin: 60px auto;

        @include sm {
          width: 100px;
          height: auto;
        }

        .links {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-around;
          justify-content: space-evenly;
          width: 132px;

          @include sm {
            flex-direction: column;
            width: 100%;
          }



          a {
            padding: 0 4px 12px;

            &:hover {
              transition: color 0.3s ease, background-color 0.3s ease, border 0.3s ease;
            }
          }

          img {
            border-radius: 0;
            width: 32px;

            &:hover {
              transition: opacity 0.3s ease;
              opacity: 0.5;
            }
          }
        }
      }
    }
  }
}

.all-posts-container {
  display: flex;
  flex-direction: column;

  //DATE DIVIDER
  //horiztonal rule with text in the middle
  //<h2 class="date-divider"><span>TEXT HERE</span></h2>
  h2.date-divider {
    width: 100%;
    display: flex;
    font-style: italic;
    margin: 20px 0 10px;

    span {
      font-family: $font-family-sans-serif;
      font-weight: 100;
      font-size: $font-size-large;
    }

    &::before, &::after {
      content: "";
      color: $black;
      flex: 1;
      border-bottom: 1px solid $black;
      // margin: auto 1em;
    }

    &::before {
      margin: auto 1em auto 0;
    }

    &::after {
      margin: auto 0 auto 1em;
    }
  }

  .blog-card {

    &.card {
      width: 100%;
      padding: 20px;
      margin: 20px auto;
    }

    &-extras {
      margin-top: 10px;
    }
  }

  .blog-post {

    &-content {
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .blog-card-title {
        color: #4e4b6d;
        text-decoration: none;

        &:hover {
          color: #4e4b6d;
          text-decoration: underline;
          cursor: pointer;
        }
      }

      .copy {
        width: 100%;
        height: auto;
        overflow: hidden;
        margin-bottom: 10px;

        @include sm {
          width: 88%;
          text-overflow: ellipsis;
          height: auto;
          margin-bottom: 0;
        }
      }
    }
  }
}
