// @import 'buttons'; // questionnaire styles
// @import 'test/test-breakpoints';
// @import 'test/test-headings';
/// z-indices reference ///
// 99 - .navigation (navigation)
// 99 - .menu-trigger (navigation)
/// fonts reference ///
// $font-family-heading: Asap - 700
// $font-family-base: Montserrat - 500, 600, 700
/// media queries reference ///
// @include xs >= 480px
// @include sm >= 768px
// @include md >= 992px
// @include lg >= 1200px
// @include xl >= 1440px
// @include xxs < 480px - hack
/// general ///

body,
html {
	background-color: $body-bg;
	color: $text-color;
	width: 100%;
	min-width: 320px; // this is to stop me going insane
	overflow-x: hidden; // fix strange iOS horizontal scrollbar
	font-family: $font-family-base;
	font-size: $font-size-base; // set base font size to override absolute font sizes
	font-weight: $font-medium;
	line-height: $line-height-base;
}

@include hero-gradients();

// constrain max width of elements

.container,
nav {
	max-width: 960px;
}
/// typography ///

p {
	margin: 0 0 1.25em;
	font-size: $font-size-base;
}
// headings

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: $font-family-heading;
	font-weight: $font-bold;
	line-height: 1.07;
	margin: 0 0 0.5em;

	a,
	a:active,
	a:focus,
	a:hover {
		color: inherit;
	}
}

//definition lists
dd {
	margin: 0;
}

// mobile heading sizes
// scale 1.134 (based on http://type-scale.com/)

h1 {
	font-size: 1.875rem; // ~30px
}

h2 {
	font-size: 1.654rem; // ~26px
}

h3 {
	font-size: 1.458rem; // ~23px
}
// desktop heading sizes
// scale 1.2129 augmented (based on http://type-scale.com/)
@include sm {
	h1 {
		font-size: 2.625rem; // ~42px
	}

	h2 {
		font-size: 1.784rem; // ~28px
	}

	h3 {
		font-size: 1.471rem; // ~23px
	}
}

h4 {
	font-size: 1.213rem; // ~19px
}

h5 {
	font-size: 1rem; // ~16px
}

h6 {
	font-size: 0.75rem; // $font-size-small; // ~12px
}
// only have a top margin if there is something before the heading
// * + h1, * + h2, * + h3, * + h4, * + h5, * + h6 {
// 	margin: 1.414em 0 .5em;
// }
// only have a top margin if there is a paragraph before the heading

p + h1,
p + h2,
p + h3,
p + h4,
p + h5,
p + h6 {
	margin: 1.414em 0 0.5em;
}
// smaller gaps between consecutive headings

h1 + h2,
h1 + h3,
h1 + h4,
h1 + h5,
h2 + h3,
h2 + h4,
h2 + h5,
h3 + h4,
h3 + h5,
h4 + h5 {
	margin-top: -0.3em;
}
// blockquote

.testimonial {
	position: relative;
	color: #4e4b6d;
	font-size: 20px;
	margin: 0 auto;
	padding: 25px;
	max-width: 960px;
}

blockquote {
	position: relative;
	color: $blockquote-text-color;
	font-size: $blockquote-text-size;
	margin: 0 auto;
	padding: 0 0 16px;
	font-weight: $font-medium;
	font-style: italic;
	text-align: center;

	@include sm {
		padding: 0 25px 16px;
	}

	p {
		margin: 0;
		font-size: $blockquote-text-size;

		& + p {
			margin-top: 0.8em;
		}

		&:first-of-type::before {
			content: url("/assets/images/66.svg");
			margin-right: 0.5em;
			vertical-align: bottom;
			// filter: invert(80%) sepia(11%) saturate(2074%) hue-rotate(145deg) brightness(91%) contrast(87%);
		}

		&:last-of-type::after {
			content: url("/assets/images/99.svg");
			margin-left: 0.5em;
			vertical-align: top;
			// filter: invert(80%) sepia(11%) saturate(2074%) hue-rotate(145deg) brightness(91%) contrast(87%);
		}
	}
	h3 {
		margin: 0;
		// font-size: $blockquote-text-size;

		& + h3 {
			margin-top: 0.8em;
		}

		&:first-of-type::before {
			content: url("/assets/images/66.svg");
			margin-right: 0.5em;
			vertical-align: bottom;
			// filter: invert(80%) sepia(11%) saturate(2074%) hue-rotate(145deg) brightness(91%) contrast(87%);
		}

		&:last-of-type::after {
			content: url("/assets/images/99.svg");
			margin-left: 0.5em;
			vertical-align: top;
			// filter: invert(80%) sepia(11%) saturate(2074%) hue-rotate(145deg) brightness(91%) contrast(87%);
		}
	}
}

