.author,
small {
	color: $blockquote-author-color;
	font-size: $blockquote-author-size;
	font-weight: $font-medium;
	font-style: normal;
	text-align: center;
	display: block;
}

// text helpers

.text-nowrap {
	display: inline-block;
}

.text-center {
	text-align: center;
}

.text-small {
	font-size: $font-size-small;
}

.text-medium {
	font-size: $font-size-medium;
}

.text-large {
	font-size: $font-size-large;
}

.text-black {
	color: $black;
}

.text-white {
	color: $white;
}

.text-pink {
	color: $brand-pink;
}

.bang-after,
.bang-before,
a.bang-after,
a.bang-before {
	position: relative;

	&::after {
		position: absolute;
		display: block;
		top:0;
		@include md {
			top: calc(50% - 0.7em);
		}
		content: "!";
		width: 30px;
		height: 30px;
		border: 3px solid currentColor;
		border-radius: 30px;
		font-family: $font-family-base;
		font-size: 20px;
		font-weight: 600;
		padding: 0 9.5px;
		line-height: 25px;
	}
}

.bang-before,
a.bang-before {
	padding-right: 40px;
  padding-left: 40px;
	&::after {
		left:0;
	}
}

.bang-after,
a.bang-after {
	padding-right: 40px;

	&::after {
		right: 0;
	}
}
// links

.no-link-styles {
	text-decoration: none;
	&:hover {
		text-decoration: none;
	}
}

a,
a:active,
a:focus,
button,
button:focus {
	@include box-shadow(none);
	outline: 0;
	font-weight: $font-semi-bold;
}

a,
a:active {
	color: $link-color;
	text-decoration: none;
}

a:focus,
a:hover {
	color: $link-hover-color;
	text-decoration: underline;
}



/// buttons ///
// button ($color, $background, $border)

.btn,
button {
	@include button($white, $primary, $primary);
}
// button variations ($color, $background, $border)

.btn-wire {
	@include button-wire($black, $white, $primary);
}

.btn-white {
	@include button($brand-purple, $white, $white);
}

.btn-pink {
	@include button($white, $brand-pink, $brand-pink);
}

.btn-purple {
	@include button($white, $brand-purple, $brand-purple);
}

.btn-arrow {
	padding-right: 45px;

	&::after {
		position: absolute;
		content: url("/assets/images/circle-right.svg");
		width: 30px;
		height: 30px;
		right: 4px;
		top: calc(50% - 15px);
	}
}
// animate hover transitions

.btn,
a,
button {
	@include transition(
		color 0.3s ease,
		background-color 0.3s ease,
		border 0.3s ease
	);

	&:focus,
	&:hover {
		@include transition(
			color 0.3s ease,
			background-color 0.3s ease,
			border 0.3s ease
		);
	}
}
/// forms ///

input,
textarea {
	border-radius: 4px;
	font-size: $font-size-medium;
}

input {
	height: 36px;
	padding: 5px 12px;
}

textarea {
	padding: 9px 12px;
}

label {
	margin: 0 0 2px 5px;
	font-size: $font-size-small;
}

.form-actions {
	text-align: right;
}
/// images ///

img {
	height: auto;
	max-width: 100%;
}
/// grid framework ///

.container {
	padding-bottom: 15px;
	padding-top: 15px;
}
/// header ///

//skip to main content link for accessebility
header .skip-link {
	background: $brand-purple;
	width: 150px;
  color: #fff;
  font-weight: 700;
  left: calc(50% - 75px);
	text-align: center;
  padding: 6px;
  position: absolute;
	border-radius: 0 0  $border-radius-base $border-radius-base;
  transform: translateY(-100%);

	&:focus {
		transform: translateY(0%);
	}
}


header .container {
	padding: 15px 15px 5px;
	@include sm {
		padding: 5px 15px;
	}
	@include md {
		padding: 15px;
	}
}

.logo {
	@include make-xs-column(10);
	// @include xs {
	// 	margin: -10px 0;
	// }
	// @include md {
	// 	margin: 0;
	// }
	a {
		display: block;
		height: 50px;
		width: 220px;
		background: url("/assets/images/justathought.svg") no-repeat;
		background-size: 220px;
		background-position: center;
		// @include xs {
		// 	height: 70px;
		// }
		@include sm {
			background: url("/assets/images/justathoughtlogo.svg") no-repeat;
			background-position: center;
			background-size: 220px;
			height: 70px;
		}
		@include md {
			margin: 10px 0;
		}
		@include xxs {
			// border: 1px solid #f00;
			// padding: 5px 0;
		}
	}

	a,
	a:active,
	a:active:hover,
	a:focus,
	a:focus:hover,
	a:hover {
