		color: transparent;
	}
}
/// navigation ///
// active states

.wrapper.about .nav-about,
.wrapper.anxiety .nav-anxiety,
.wrapper.covid19 .nav-covid19,
.wrapper.depression .nav-depression,
.wrapper.mixed .nav-mixed,
.wrapper.clinicians .nav-clinicians,
.wrapper.contact .nav-contact,
.wrapper.faq .nav-faq,
.wrapper.home .nav-home,
.wrapper.quiz .nav-quiz,
.wrapper.news .nav-news,
.wrapper.post .nav-news {
	@include nav-active;

}

.wrapper.course-page .nav-courses,
.wrapper.covid19 .nav-courses,
.wrapper.anxiety .nav-courses,
.wrapper.depression .nav-courses,
.wrapper.mixed .nav-courses {

	& > a {
		border-#{$menu-active-direction}: $menu-active-border-width solid $menu-link-active-color;
		color: $menu-link-active-color;
		cursor: default;
		@include md {
			border-#{$menu-active-direction}: 0;
			border-#{$navbar-active-direction}: $navbar-active-border-width solid $navbar-link-active-color;
		}

		transition-duration: 0s;
		transition-delay: 0.4s;
		cursor: pointer;
	}

}


html *:focus {
	outline: 2px solid $brand-purple;
	outline: 1px auto $brand-purple;
}

html *:focus:not(:focus-visible) {
	outline: none;
}

html *:focus-visible {
	outline: 1px auto $brand-purple;
}

/// main ///

.gradient {
	background: #dee9fe; // fallback
	background: radial-gradient(
			circle at top,
			rgba(#bddfff, 1) 10%,
			rgba(#dee9fe, 1) 30%,
			rgba(#dee9fe, 0) 40%
		),
		radial-gradient(
			circle at -30% 60%,
			rgba(#6371b6, 0.8) 0%,
			rgba(#6371b6, 0) 30%
		),
		radial-gradient(
			circle at top right,
			rgba(#fcfafe, 1) 10%,
			rgba(#fbe7f7, 1) 20%,
			rgba(#fbe7f7, 0) 40%
		),
		linear-gradient(8deg, #d7faff 30%, #e8f9fe 60%);
	@include box-shadow(
		inset 0 -2px 0 #fff,
		inset 0 2px 0 #fff
	); // fix background leak at certain sizes

	.nav-container + main > & {
		@include box-shadow(
			inset 0 -2px 0 #fff
		); // remove top shadow from gradient if immediately after nav
	}
}

