.accordion-item:last-of-type .content,
.accordion-item:last-of-type a {
	border-bottom: none;
}

ul.accordion {
	list-style: none;
	padding-left: 0;
}

.accordion a {
	border-bottom: 1px solid $brand-logo;
}

.accordion a, .accordion h2 {
	position: relative;
	display: block;
	width: 100%;
	padding: 2rem 2rem 2rem 0;
	color: $black;
	font-size: $font-size-large;
	font-weight: $font-bold;
	margin-bottom: 0;

	&:hover,
	&:hover::after {
		cursor: pointer;
		color: $brand-teal;
		text-decoration: none;
	}

	&:hover::after {
		border: 2px solid $brand-teal;
	}

	&.active {
		color: $black;
		border-bottom: 0 solid transparent;

		&:hover::after {
			color: $brand-teal;
			border: 2px solid $brand-teal;
		}
	}

	&.active::after {
		content: "-";
		color: $brand-logo;
		border: 2px solid $brand-logo;
		line-height: 22px;
	}
}

.accordion h2 {
	padding: 0;
	font-family: inherit;
}

.accordion a::after {
	content: "+";
	position: absolute;
	right: 0;
	top: calc(50% - 13px);
	font-size: 2rem;
	line-height: 25px;
	font-weight: 400;
	color: $brand-logo;
	// padding: 5px;
	width: 30px;
	height: 30px;
	border-radius: 50%;
	border: 2px solid $brand-logo;
	text-align: center;
	@include sm {
		right: 1rem;
	}
}

.accordion .content {
	opacity: 0;
	padding: 0;
	max-height: 0;
	border-bottom: 1px solid $brand-logo;
	overflow: hidden;
	clear: both;
	transition: all 0.2s ease 0.15s;
}

.accordion .content p {
	font-size: 1rem;
	font-weight: 400;
}

.accordion .content.active {
	opacity: 1;
	padding: 0 0 1rem;
	max-height: 100%;
	transition: all 0.35s ease 0.15s;
}
