
.contact{
  .gradient{
    height: auto;
    // min-height: calc(100vh - 200px);
    // @include md {
    //   min-height: calc(100vh - 200px);
    //   // margin-bottom: -50px;
    //   height: auto;
    // }
  }

    footer {
      padding-top: 0;
      .container {
        padding-top:160px;
      }
    }

  .message-form {
    .req {
      color: $error-red-gradient-bg;
      text-decoration: none;
    }

    #error-message {
      display: flex;
      flex-direction: column;
    }

    //for non label elements that should look like the labels
    .label {
      margin: 0 0 2px 5px;
      font-size: 12px;
    }

    input, textarea {
      box-shadow: none;
    }

    input[aria-invalid=true], textarea[aria-invalid=true] {
      // outline: $error-red-gradient-bg solid 2px; use outline again once outline-radius is available on more than just firefox TODO
      outline: none;
      box-shadow: 0px 0px 0px 2px $error-red-gradient-bg;
    }

    input[aria-invalid=false], textarea[aria-invalid=false] {
      // outline: $error-red-gradient-bg solid 2px; use outline again once outline-radius is available on more than just firefox TODO
      outline: none;
      box-shadow: 0px 0px 0px 2px $success;
    }

    .statusBox {
      flex-grow: 2;
      .error-icon {
        display: none;
        float:right;
        margin-bottom: 2px;
        font-size: 15px;
        color: #c50404;
      }
      .success-icon {
        display: none;
        float:right;
        margin-bottom: 2px;
        font-size: 16px;
        color: $success;
      }
    }

    .badValue {
      .error-icon {
        display: block;
      }
      .success-icon {
        display: none;
      }
    }

    .goodValue {
      .error-icon {
        display: none;
      }
      .success-icon {
        display: block;
      }
    }
  }


  footer{
    position: relative;
    @include md {
      // bottom: 0px;
      // position: absolute;
      left: 0;
      right: 0;
    }
  }
}
