// _mixins (_variables)
@import 'mixins';

.nav-container {
	position: relative;
}
nav {
	position: absolute;
	top: 0;
	right: 0;
}

.navigation {
	display: none;
	position: absolute;
	top: -70px;
	right: 0;
	min-height: 100vh;
	width: $menu-width;
	list-style: none;
	margin: 0;
	padding: $menu-padding;
	background-color: $menu-bg;
	color: $menu-color;
	z-index: 99;
	font-size: 0; // removes gaps between li items

	@include sm {
		top: -80px;
	}

	li {
		font-size: $menu-font-size;
		margin: 0;
		padding: 0;

		&::before {
			display: none;
		}
		// remove last separator
		// &:last-of-type a {
		//     border-bottom: 0;
		// }
	}

	a {
		display: block;
		width: 100%;
		padding: $menu-link-padding;
		margin: $menu-link-spacing 0;
		color: $menu-link-color;
		text-transform: $menu-text-transform;
		text-align: left;
		// seperator
		border: 0;
		border-bottom: $menu-separator-width solid $menu-color;
		// transparent border to counter active
		border-#{$menu-active-direction}: $menu-active-border-width solid transparent;

		&:focus,
		&:hover {
			text-decoration: none;
			background-color: $menu-link-hover-bg;
			color: $menu-link-hover-color;
		}

		&:active {
			text-decoration: none;
			background-color: $menu-link-active-bg;
			color: $menu-link-active-color;
		}
	}
}
// sub menu dropdown

li.sub-menu {
	position: relative;
	padding: 5px 0;

	> a {
		padding-bottom: 10px;
		cursor: default;

		&:active,
		&:active:hover,
		&:focus,
		&:focus:hover,
		&:hover {
			color: $menu-color;
		}
	}

	ul {
		list-style: none;
		padding: 0;

		&::after,
		&::before {
			display: block;
			position: absolute;
			top: 0;
			left: 32px;
			content: '';
			width: 40px;
			height: 3px;
			background-color: $menu-color;
		}

		&::after {
			top: auto;
			bottom: 0;
		}
	}

	li {
		margin: 0;
		padding: 0;

		&:last-of-type a {
			margin-bottom: 20px;
		}

		a {
			padding: 5px 20px 5px 30px;
			font-weight: 400;
			font-size: $font-size-medium;
		}
	}
}
// login button

li.nav-login,
span.nav-login {
	text-align: center;
	a {
		@include button($white, $brand-purple, $brand-purple);
		width: auto;
		height: auto;
		min-width: 80%;
		text-align: center;

		&.nav-signup {
			@include button($white, $brand-pink, $brand-pink);
			padding-top: 2px;
			padding-bottom: 2px;
			margin-left: 8px;
		}
	}
}

span.nav-login {
	display: flex;
	margin-top: 24px;

	a {
		min-width: auto;
	}
}
// help button

.nav-help {
	text-align: center;
}

.nav-help a {
	@include button-wire($black, $white, $brand-purple);
	display: inline-block;
	width: auto;
	margin: 5px auto 0;
	text-align: center;
	padding-right: 6px;

	&.bang-after {
		padding-right: 45px;

		&::after {
			top: calc(50% - .8em);
			right: 5px;
		}
	}
	@include md {
		&.bang-after {
			padding-right: 40px;

			&::after {
				right: 0;
			}
		}
	}
}
// responsive menu

.menu-trigger {
	@include transition(all 0.3s ease-in);
	display: block;
	-webkit-appearance: none;
	position: absolute;
	bottom: 10px;
	width: 50px;
	height: 50px;
	padding: 0;
	background-color: $navbar-toggle-bg;
	text-align: center;
	z-index: 99;
	right: 5px;
	border-radius: 25px;
	@include sm {
		right: 15px;
		bottom: 15px;
	}

	&:focus {
		outline: $brand-purple auto 1px;
	}

	&:hover {
		background-color: $navbar-toggle-hover-bg;
		text-decoration: none;
		cursor: pointer;

		.menu-icon,
		.menu-icon::after,
		.menu-icon::before {
			background-color: $navbar-toggle-hover-color;
		}
	}

	&.is-open {
		@include transition(all 0.4s ease-out);
		// right: $menu-width;
		margin-right: 5px;
		background-color: $navbar-toggle-active-bg;
	}

	&.is-closed {
		@include transition(all 0.3s ease-in);
	}
}

.menu-icon {
	display: block;
	@include transition(transform 0.15s ease);
	top: 22.5px;
	margin: 0 10px;

	&,
	&::after,
	&::before {
		background-color: $navbar-toggle-color;
		border-radius: 4px;
		height: 5px;
		position: absolute;
		width: 30px;
	}

	&::after,
	&::before {
		content: '';
		display: block;
	}

	&::before {
		@include transition(top 0.075s 0.12s ease, transform 0.15s ease, opacity 0.075s ease);
		top: -8px;
	}

	&::after {
		bottom: -8px;
		@include transition(bottom 0.075s 0.12s ease, transform 0.075s cubic-bezier(.55, .055, .675, .19));
	}
}

.is-open .menu-icon {
	transform: rotate(45deg);
	transition-delay: 0.12s;
	transition-timing-function: cubic-bezier(.215, .61, .355, 1);

	&,
	&::after,
	&::before {
		background-color: $navbar-toggle-active-color;
	}

	&::before {
		opacity: 0;
		top: 0;
		@include transition(top 0.075s ease, opacity 0.075s 0.12s ease);
	}

	&::after {
		bottom: 0;
		transform: rotate(-90deg);
		@include transition(bottom 0.075s ease, transform 0.075s 0.12s cubic-bezier(.215, .61, .355, 1));
	}
}

.hide-overflow {
	overflow: hidden;
	background-color: $body-bg;
	@include transition(background 0.4s ease-out);

	footer,
	header,
	main {
		opacity: 0.05;
		@include transition(opacity 0.4s ease-out);

		a {
			visibility: hidden;
		}
	}
}
// desktop nav
@include md {
	.nav-container {
		background-color: $navbar-bg;
		border-top: $navbar-border-width solid $navbar-border-color;
		border-bottom: 3px solid $navbar-border-color;
	}

	.menu-trigger {
		display: none;
		right: 0;
	}

	nav {
		@include make-container();
		position: relative;
		// max-height: $navbar-height; // overflow: hidden;
		min-height: $navbar-height;
	}

	.navigation {
		@include make-xs-column(12);
		display: block;
		position: relative;
		top: 0;
		min-height: $navbar-height;
		// height: $navbar-height;
		width: 100%;
		text-align: left;
		padding: 0;
		background-color: $navbar-bg;
		color: $navbar-color;
		font-size: 0; // removes gaps between li items

		li {
			font-size: $navbar-font-size;
			position: relative;
			display: inline-block;
			height: $navbar-height;
			padding: 0 20px;

			&:nth-of-type(3) {
				padding-left: 0;
			}
			// remove last separator
			// &:last-of-type a {
			// 	border-right: 0;
			// }
			// image or text separator
			// &:nth-of-type(1n+3)::after {
			// content: '\2022';
			// position: absolute;
			// height: $navbar-height;
			// top: (($navbar-height - $line-height-computed) / 2);
			// font-size: 10px;
			// left: -5px;
			// }
		}

		a {
			float: left;
			height: $navbar-height;
			padding: $navbar-link-padding;
			line-height: $navbar-height/2;
			margin: 0 $navbar-link-spacing;
			// separator
			border: 0;
			border-right: $navbar-separator-width solid $navbar-color;
			// transparent border to counter active
			border-#{$navbar-active-direction}: $navbar-active-border-width solid transparent;

			&:focus,
			&:hover {
				background-color: $navbar-link-hover-bg;
				color: $navbar-link-hover-color;
			}

			&:active {
				background-color: $navbar-link-active-bg;
				color: $navbar-link-active-color;
			}
		}
	}
	// hide HOME in desktop nav
	li.nav-home {
		display: none;
	}
	// sub menu dropdown
	li.sub-menu {
		height: auto;
		padding: 0 10px 0 15px;

		&.open {
			background-color: $white;

			ul {
				display: block;
				width: 100%;
			}
		}

		& > a {
			padding-right: 40px;
			cursor: pointer;

			&:hover {
				color: $navbar-link-hover-color;
			}

			&::after {
				@include transition(background-color 0.3s ease);
				display: block;
				position: absolute;
				content: '';
				top: calc(50% - 4px);
				right: 25px;
				width: 0;
				height: 0;
				border-style: solid;
				border-width: 8px 4px 0 4px;
				border-color: currentColor transparent transparent transparent;
			}
		}

		ul {
			display: none;
			margin: 0;
			padding: 0;
			position: absolute;
			top: 50px;
			background-color: $white;
			margin-left: -15px;

			&::after,
			&::before {
				display: none;
			}

			li {
				display: block;
				margin: 0;
				height: auto;
				min-height: 50px;
				overflow: auto;
				padding: 0;

				&:last-of-type a {
					margin-bottom: 0;
				}

				a {
					padding: 10px 10px 10px 15px;
					height: auto;
					min-height: 50px;
					font-size: $font-size-small;
				}
			}
		}
	}
	// login button
	li.nav-login {
		position: absolute;
		right: 0;
		top: -85px;
		padding: 7.5px 0 0;

		a {
			width: auto;
			min-width: auto;
			min-width: -webkit-min-content;
			padding: 2px 15px;
			font-size: $navbar-font-size;
		}
	}
	// help button
	li.nav-help {
		position: relative;
		float: right;
		padding: 0;

		a {
			font-size: $navbar-font-size;
			padding: 10px 0 0 15px;
			border: none;
			margin: 1px 0;
			border-radius: 0;
		}
	}
}
// active state
@mixin nav-active {
	a {
		border-#{$menu-active-direction}: $menu-active-border-width solid $menu-link-active-color;
		color: $menu-link-active-color;
		cursor: default;
		@include md {
			border-#{$menu-active-direction}: 0;
			border-#{$navbar-active-direction}: $navbar-active-border-width solid $navbar-link-active-color;
		}
	}
}


