//ACCESSIBILITY KEYBOARD SUPPORT SUBMENU
@include md {
	//IE11 support
	.sub-menu li a:focus {
		border: 2px solid $brand-purple;
	}

	.sub-menu .open-sub-menu {
		visibility: visible;
		display: block;
		width: 100%;
	}
	//

	.sub-menu li:focus-within a {
		border: 2px solid $brand-purple;
	}


	.sub-menu > a:hover + ul,
	.sub-menu > a:focus + ul {
	    visibility: visible;
			display: block;
			width: 100%;
	}

	.sub-menu:focus-within > ul {
			visibility: visible;
			display: block;
			width: 100%;
	}

	.sub-menu:focus-within {
		background: #FFF;
	}
}
