
.courses {
	.section-heading {
		margin-top: 60px;

		img {
		 max-width:50%;
		}
		p {
			text-align: left;
		}
	}

	.row + .card {
		margin-top: 30px;
	}
}

.course-name {
	@include make-sm-column(12);
	font-size: $font-size-large;
	font-weight: $font-bold;
}

.course-thumb {
	@include make-xs-column(7);
	@include make-sm-column(5);
	@include make-sm-column-push(7);
	@include make-xxs-column(12);
	@include column-center-xs;
	padding: 15px;
	@include sm {
		padding: 0 15px;
	}
}

.course-blurb {
	@include make-sm-column(7);
	@include make-sm-column-pull(5);

	h1, .heading--large {
		margin-top: 0.3em;
		color: $brand-teal;
		font-style: italic;
	}
}

.pad-bottom {
	padding-bottom: 100px;
}

.courses .card {
	// alternate layout left - right
	&:nth-of-type(2n + 1) {
		.course-name {
			@include make-sm-column(7);
			@include make-sm-column-push(5);
		}

		.course-thumb {
			@include make-sm-column-push(0);
		}

		.course-blurb {
			@include make-sm-column-pull(0);
		}
	}
	// vertically center content
	// @include sm {
	// 	.row {
	// 		display: flex;
	// 		.course-thumb, .course-blurb {
	// 			margin-top: auto;
	// 			margin-bottom: auto;
	// 		}
	// 	}
	// }
}

.lineart-side-panel {
	background: url(/assets/images/Mai-top-swish-inverted.png),
		linear-gradient(#fff 1%, #d3f0f5 1%);
	background-repeat: repeat-x;
	background-position: 35% 0%;

	@include sm {
		background: url(/assets/images/Mai-top-swish-inverted.png),
			url(/assets/images/lineart/leftpanel.png),
			url(/assets/images/lineart/rightpanel.png),
			linear-gradient(#fff 1%, #d3f0f5 1%);
		background-repeat: repeat-x, no-repeat, no-repeat;
		background-position: 35% 0%, left 221px, right 221px;
	}
}

.course-cards {
	// background: url(/assets/images/Mai-top-swish-inverted.png),
	// 	url(/assets/images/lineart/leftpanel.png),
	// 	url(/assets/images/lineart/rightpanel.png),
	// 	linear-gradient(#fff 1%, #d3f0f5 1%);
	padding-top: 15px;
	padding-bottom: 80px;
	// background-repeat: repeat-x, no-repeat, no-repeat;
	// background-position: 35% 0%, left 221px, right 221px;
}

.course-grid {
	display:flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	@include md {
		justify-content: flex-start;
	}

	align-items: flex-start;
	// display: grid;
	// grid-gap: 1em;
	// grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
	// grid-template-rows: repeat(auto-fill, 400px);

	text-align: center;
	text-align: -webkit-center;
	text-align: -moz-center;
	padding-left:0;

	.course-card {
		display: flex;
		flex-direction: column;
		align-items: center;
		box-sizing: border-box;
		@include xs {
			flex: 0 0 90%;
			max-width: 300px;
		}
		@include sm {
			flex: 0 0 45%;
		}
		@include md{
			flex: 0 0 33.333333%;
		}



		// display: grid;
		// grid-template-columns: auto;
		// grid-template-rows: 200px 200px;
		padding: 0;
		// max-width: 275px;
		min-width: 250px;
		width: 100%;
		min-height: 400px;

		margin: 0 10px 24px 0;
		border-radius: 8px;

	&.empty-card {

		border: 3px solid $text-color;
		justify-content: center;
		color: $text-color;
		justify-self: center;

		img{
			display: block;
			margin: auto;
			max-width:130px;
		}
		span {
			font-size: $font-size-large;
			width: 125px;
		}
	}

	&.card {
		border: none;
		background: $off-white;
		justify-content: flex-start;
		.course-copy {
			display:flex;
			flex-direction: column;
			// display: grid;
			// grid-template-rows: 150px 50px;
			// grid-row-start: 2;
			// grid-row-end: span 3;
			padding: 0 30px;
			text-align: center;
			flex-grow: 1;

			.course-info {
				// grid-row-start: 1;
				// grid-row-end: span 1;
				// display:flex;
				flex-grow: 1;

				a {
					color: #4e4b6d;
		      text-decoration: none;

		      &:hover {
		        color: #4e4b6d;
		        // text-decoration: underline;
		        cursor: pointer;
		      }
				}

				.course-heading {
					// grid-row-start: 1;
					// grid-row-end: span 1;
					font-family: $font-family-heading;
					font-size: 1.25rem;
					// margin: 15px 0 0;
					letter-spacing: 0.5px;
					margin: 15px auto;
					font-style: italic;
					// width: 215px;
					width: auto;
					display: block;
					line-height: 1.07;
				}

				h2 {
					// grid-row-start: 2;
					// grid-row-end: span 1;
					line-height: unset;
					margin: 0;
					font-size: $font-size-medium;
					width: 215px;
					font-weight: 500;
					font-family: $font-family-base;
				}
			}

			a {
				// grid-row-start: 3;
				// grid-row-end: span 1;
				flex-direction: row;
				align-items: flex-end;
				font-size: 16px;
				padding-bottom:32px;
				@include sm {
					font-size: 14px;
				}
			}

			.read-more {
				// grid-row-start: 2;
				// grid-row-end: span 1;
			}

			.arrow-btn {
				display: inline-block;
				border-radius: 50%;

				&-inside {
					display: inline-block;
					width: 20px;
					height: 20px;
					border: 2px solid $brand-logo;
					border-radius: 50%;
					margin-left: 2pt;
				}

				&-inside:after {
					content: "";
					display: inline-block;
					margin-top: 0px;
					margin-left: -3px;
					width: 10px;
					height: 10px;
					border-top: 2px solid $brand-logo;
					border-right: 2px solid $brand-logo;
					-moz-transform: rotate(45deg);
					-webkit-transform: rotate(45deg);
					transform: rotate(45deg);
				}
			}
		}

		.course-thumbnail {
			// grid-row-start: 1;
			// grid-row-end: span 1;
			height: 200px;
			width: 100%;
			background-size: contain;
			background-repeat: no-repeat;
			border-radius: 8px 8px 0 0;
			background-size: cover;
		}
	}
	}
}

.more-courses {
	font-family: $font-family-heading;
	font-size: $font-size-extra-large;
	font-weight: $font-bold;
	font-style: italic;
	text-align: center;
}

// courses

.box-stories {
	text-align: center;

	.card {
		margin-top: 0;
	}

	h1, .heading--large {
		margin: 5px auto 45px;
	}

	.btn {
		margin: auto;
		display: inline-block;
	}
}

.stories-grid {
	@include make-block-grid(1, 1, 2, 3, 3);
	text-align: left;
}

.story-block {
	padding-left: 90px;

	img {
		position: absolute;
		left: 0;
		top: -10px;
		width: 80px;
		height: 80px;
	}
}

.stats-text {
	@include make-sm-column(5);
}

.stats-pie {
	@include make-sm-column(3.5);
	text-align: center;
	font-weight: $font-bold;
	margin-top: 45px;
	@include sm {
		margin-top: 0;
	}
}

.steps .row {
	//alternate text and image
	&:nth-last-of-type(2) {
		.step-text {
			@include make-sm-column-push(6);
		}

		.step-image {
			@include make-sm-column-pull(6);
			display: block;
		}
	}
	@include sm {
		display: flex;
	}
}

.step-heading {
	@include make-sm-column(12);
	text-align: center;
	margin-bottom: 30px;
}

.step-image,
.step-text {
	@include make-sm-column(6);
	padding-top: 30px;
	padding-bottom: 30px;

	h2 {
		margin-bottom: 0;
	}

	dd {
		margin-left: 60px;
		margin-bottom: 1.25em;
	}
	@include sm {
		margin-top: auto;
		margin-bottom: auto;
	}
}

.step-image {
	@include make-xs-column(7);
	@include make-xxs-column(10);
	@include column-center-xs;
	display: none;
	@include sm {
		display: block;
	}
}

.step-number {
	background-color: $brand-blue;
	color: $white;
	padding: 9px;
	border-radius: 50px;
	display: inline-block;
	width: 49px;
	height: 49px;
	text-align: center;
	margin-right: 10px;
}

.duration-text {
	@include make-sm-column(7);
	margin-bottom: 60px;
}

.duration-image {
	@include make-sm-column(5);
}
