/// colors ///
// black & white
$white:                         #ffffff;
// $black:                         #4e4b6d;
$black:                         #43415e;

$off-white:                     #f4f8ff;

$light-purple:                  #BEA9D2;
$light-blue:                    #D3F0F5;
$light-teal:                    #5cc3da;
$dark-purple:                   #7C45A4;

// gray (test here - https://codepen.io/charlie/pen/ozXppQ)
$gray:                          #909295;
$terms-gray:                    #74767c;

$gray-lightest:                 mix(#fff, $gray, 85%); // #eeefef
$gray-lighter:                  mix(#fff, $gray, 50%); // #c8c9ca
$gray-light:                    mix(#fff, $gray, 20%); // #a6a8aa
$gray-dark:                     mix(#000, $gray, 20%); // #737577
$gray-darker:                   mix(#000, $gray, 50%); // #48494b
$gray-darkest:                  mix(#000, $gray, 85%); // #161616

// brand colors
// $brand-pink:                    #f06292;
// $brand-pink:                    #CB4579;
$brand-pink:                    #c9467f;
$brand-yellow:                  #ffd54f;
$brand-blue:                    #5ac7da;
$brand-logo:                    #6884aa;

// $brand-teal:                    #009096;
$brand-teal:                    #007e84;
$brand-purple:                  #6371b6;

$system-green:                  #90d5ac;
$system-orange:                 #ffd2bb;
$system-red:                    #e25f5f;
$error-red-gradient-bg:         #c50404;

// brand extras
$brand-blue-transparent:        rgba($brand-blue, 0.6);
$hero-purple:                   #686CD3;

// system colors
$primary:                       $brand-pink;
$success:                       $system-green;
$info:                          $brand-blue;
$warning:                       $system-orange;
$danger:                        $system-red;

/// scaffolding ///
// background color for `<body>`.
$body-bg:                       $white;
// global text color on `<body>`.
$text-color:                    $black;

// global text link color.
// $link-color:                    $brand-purple;
$link-color:                    #606eb0;
// link hover color set via `darken()` function.
$link-hover-color:              darken($link-color, 20%);

/// typography ///
//
// font, line-height, and color for body text, headings, and more.

$font-family-sans-serif:        'Montserrat', Helvetica, Arial, sans-serif;
$font-family-serif:             Georgia, 'Times New Roman', Times, serif;
// default monospace fonts for `<code>`, `<kbd>`, and `<pre>`.
$font-family-monospace:         menlo, monaco, consolas, 'courier new', monospace;

$font-family-heading:           'Asap', 'Montserrat', Helvetica, Arial, sans-serif;
$font-family-base:              $font-family-sans-serif;

$font-size-base:                16px;
$font-size-extra-small:         ceil(($font-size-base * 0.68)); // ~11px
$font-size-small:               ceil(($font-size-base * 0.75)); // ~12px
$font-size-medium:              ceil(($font-size-base * 0.875)); // ~14px
$font-size-large:               ceil(($font-size-base * 1.213)); // ~19px
$font-size-extra-large:         ceil(($font-size-base * 2.164)); // ~35px

// font weights
$font-medium:                   500;
$font-semi-bold:                600;
$font-bold:                     700;

// unit-less `line-height` for use in components like buttons.
$line-height-base:              1.375;
// computed 'line-height' (`font-size` * `line-height`) for use with `margin`, `padding`, etc.
$line-height-computed:          floor(($font-size-base * $line-height-base));

/// components ///
//
// define common padding and border radius sizes and more. values based on 14px text and 1.428 line-height (~20px to start).

$padding-base-vertical:         6px;
$padding-base-horizontal:       12px;

$padding-large-vertical:        10px;
$padding-large-horizontal:      16px;

$padding-small-vertical:        5px;
$padding-small-horizontal:      10px;

$padding-xs-vertical:           1px;
$padding-xs-horizontal:         5px;

$line-height-large:             1.3333333; // extra decimals for win 8.1 chrome
$line-height-small:             1.5;

$border-radius-base:            4px;
$border-radius-small:           3px;
$border-radius-large:           16px;

// global color for active items (e.g., navs or dropdowns).
$component-active-color:        #fff;
// global background color for active items (e.g., navs or dropdowns).
$component-active-bg:           $primary;

// width of the `border` for generating carets that indicate dropdowns.
$caret-width-base:              4px;
// carets increase slightly in size for larger components.
$caret-width-large:             5px;

/// tables ///
//
// customizes the `.table` component with basic values, each used across all table variations.

// padding for `<th>`s and `<td>`s.
$table-cell-padding:            8px;
// padding for cells in `.table-condensed`.
$table-condensed-cell-padding:  5px;

// default background color used for all tables.
$table-bg:                      transparent;
// background color used for `.table-striped`.
$table-bg-accent:               #f9f9f9;
// background color used for `.table-hover`.
$table-bg-hover:                #f5f5f5;
$table-bg-active:               $table-bg-hover;

// border color for table and cell borders.
$table-border-color:            #ddd;

/// media query breakpoints ///

// extra small screen / phone
$screen-xs-min:                 480px;

// small screen / tablet
$screen-sm-min:                 768px;

// medium screen / desktop
$screen-md-min:                 992px;

// large screen / wide desktop
$screen-lg-min:                 1200px;

// so media queries don't overlap when required, provide a maximum
$screen-xs-max:                 ($screen-sm-min - 1);
$screen-sm-max:                 ($screen-md-min - 1);
$screen-md-max:                 ($screen-lg-min - 1);

/// grid system ///

// number of columns in the grid.
$grid-columns:                  12;
// padding between columns. gets divided in half for the left and right.
$grid-gutter-width:             30px;

// navbar collapse
// point at which the navbar becomes uncollapsed.
$grid-float-breakpoint:         $screen-md-min;
// point at which the navbar begins collapsing.
$grid-float-breakpoint-max:     ($grid-float-breakpoint - 1);

/// container sizes ///

// for `$screen-sm-min` and up.
$container-sm:                  (720px + $grid-gutter-width);

// for `$screen-md-min` and up.
$container-md:                  (940px + $grid-gutter-width);

// for `$screen-lg-min` and up.
$container-lg:                  (1140px + $grid-gutter-width);

/// navigation ///

// colors
$menu-bg:                       $off-white;
$menu-color:                    $text-color; //separators & text

$navbar-bg:                     $menu-bg;
$navbar-color:                  $menu-color; //separators & text

// links
$menu-link-color:               $text-color;
$menu-link-hover-color:         $brand-teal;
$menu-link-hover-bg:            transparent;
// $menu-link-active-color:        $brand-purple;
$menu-link-active-color:        $link-color;
$menu-link-active-bg:           transparent; // darken($menu-bg, 12%);

$navbar-link-color:             $menu-link-color;
$navbar-link-hover-color:       $menu-link-hover-color;
$navbar-link-hover-bg:          $menu-link-hover-bg;
$navbar-link-active-color:      $menu-link-active-color;
$navbar-link-active-bg:         $menu-link-active-bg;

// navbar toggle
$navbar-toggle-bg:              $body-bg;
$navbar-toggle-color:           $link-color;
$navbar-toggle-hover-bg:        $body-bg;
$navbar-toggle-hover-color:     $link-hover-color;
$navbar-toggle-active-bg:       $body-bg;
$navbar-toggle-active-color:    $link-hover-color;

// menu
$menu-width:                    260px; // max width for a 320px wide screen
$menu-padding:                  15px 0;
$menu-link-padding:             10px 20px 10px 30px;
$menu-link-spacing:             5px;
$menu-font-size:                $font-size-base;
$menu-text-transform:           none;
$menu-separator-width:          0;
$menu-active-direction:         left;
$menu-active-border-width:      3px;

// navbar
$navbar-height:                 50px;
$navbar-border-color:           darken($navbar-bg, 6.5%);
$navbar-border-width:           0;
$navbar-link-padding:           12px 0;
$navbar-link-spacing:           0;
$navbar-font-size:              14px;
$navbar-text-transform:         none;
$navbar-separator-width:        0;
$navbar-active-direction:       bottom;
$navbar-active-border-width:    3px;

/// pagination ///

$pagination-color:              $link-color;
$pagination-bg:                 #fff;
$pagination-border:             #ddd;

$pagination-hover-color:        $link-hover-color;
$pagination-hover-bg:           $gray-lighter;
$pagination-hover-border:       #ddd;

$pagination-active-color:       #fff;
$pagination-active-bg:          $primary;
$pagination-active-border:      $primary;

$pagination-disabled-color:     $gray-light;
$pagination-disabled-bg:        #fff;
$pagination-disabled-border:    #ddd;

/// form states and alerts ///

$state-success-text:            #3c763d;
$state-success-bg:              #dff0d8;
$state-success-border:          darken(adjust-hue($state-success-bg, -10), 5%);

$state-info-text:               #31708f;
$state-info-bg:                 #d9edf7;
$state-info-border:             darken(adjust-hue($state-info-bg, -10), 7%);

$state-warning-text:            #8a6d3b;
$state-warning-bg:              #fcf8e3;
$state-warning-border:          darken(adjust-hue($state-warning-bg, -10), 5%);

$state-danger-text:             #a94442;
$state-danger-bg:               #f2dede;
$state-danger-border:           darken(adjust-hue($state-danger-bg, -10), 5%);

/// alerts ///

$alert-padding:                 15px;
$alert-border-radius:           $border-radius-base;
$alert-link-font-weight:        bold;

$alert-success-bg:              $state-success-bg;
$alert-success-text:            $state-success-text;
$alert-success-border:          $state-success-border;

$alert-info-bg:                 $state-info-bg;
$alert-info-text:               $state-info-text;
$alert-info-border:             $state-info-border;

$alert-warning-bg:              $state-warning-bg;
$alert-warning-text:            $state-warning-text;
$alert-warning-border:          $state-warning-border;

$alert-danger-bg:               $state-danger-bg;
$alert-danger-text:             $state-danger-text;
$alert-danger-border:           $state-danger-border;

/// breadcrumbs ///

$breadcrumb-padding-vertical:   8px;
$breadcrumb-padding-horizontal: 15px;
// breadcrumb background color
$breadcrumb-bg:                 #f5f5f5;
// breadcrumb text color
$breadcrumb-color:              #ccc;
// text color of current page in the breadcrumb
$breadcrumb-active-color:       $gray-light;
// textual separator for between breadcrumb elements
$breadcrumb-separator:          '/';

/// close ///

$close-font-weight:             bold;
$close-color:                   #000;
$close-text-shadow:             0 1px 0 #fff;

/// code ///

$code-color:                    #c7254e;
$code-bg:                       #f9f2f4;

$kbd-color:                     #fff;
$kbd-bg:                        #333;

$pre-bg:                        #f5f5f5;
$pre-color:                     $gray-dark;
$pre-border-color:              #ccc;
$pre-scrollable-max-height:     340px;

/// type ///

// horizontal offset for forms and lists.
$component-offset-horizontal:   180px;
// text muted color
$text-muted:                    $gray-light;
// abbreviations and acronyms border color
$abbr-border-color:             $gray-light;
// headings small color
$headings-small-color:          $gray-light;

// blockquote text color
$blockquote-text-color:         $black;
// blockquote text font size
$blockquote-text-size:          $font-size-large;
// blockquote quote color
$blockquote-quote-color:        $brand-blue;
// blockquote quote font size
$blockquote-quote-size:         $font-size-large * 3;
// blockquote author color
$blockquote-author-color:       $black;
// blockquote author font size
$blockquote-author-size:        $font-size-medium;

// page header border color
$page-header-border-color:      $gray-lighter;
// width of horizontal description list titles
$dl-horizontal-offset:          $component-offset-horizontal;
// point at which .dl-horizontal becomes horizontal
$dl-horizontal-breakpoint:      $grid-float-breakpoint;
// horizontal line color.
$hr-border:                     $gray-lighter;

// global spacing
$spacing: 8px;

//blog posts
$blog-content-large-padding:          2em;
$blog-content-small-padding:          1em;
