// start about/testimonials //

.testimonial-heading {
	.section-heading {
		margin-bottom: 30px;

		h1 {
			font-size: $font-size-extra-large;
		}
	}
}

.hero {
	&-display--image-text {
		display: flex;
		flex-wrap: wrap;
		flex-direction: row;
		justify-content: center;

		@media (max-width: $screen-xs-max) {
			text-align: center;
		}

		img {
			margin: 10px;
		}
		.page-copy {
			margin: 10px;
			display: flex;
			align-items: center;
		}
	}

	&-background {
		&--light-blue {
			background: $light-blue;
		}
	}
}

//background colours
.bg-colour {
	@include bg-colour();
}

section.testimonials {

	& .masonry-sizer {
    margin: 12px 0;
    width:100%;
    @include sm {
      width: calc(50% - 24px);
      margin: 12px 12px;
    }
    @include md {
      width: calc(33% - 24px);
      margin: 12px 12px;
    }
  }
	// background: linear-gradient(180deg, #fff 10%, #d5eaff 10%);
	// background: url(/assets/images/Mai-top-swish-inverted.png),
	// 	linear-gradient(#fff 1%, #d3f0f5 1%);
	padding-top: 15px;
	padding-bottom: 80px;
	// background-repeat: repeat-x;
	// background-position-x: 35%;
	// padding-bottom: 100px;

	.masonry-content {
		padding: 20px;
		margin: 0;

		.testimonial-copy-wrapper {

			p::before, p::after {
				content: none;
			}
			// display: flex;
			width: 100%;
			text-align: left;

		  p {
				font-style: normal;
				text-decoration: none;
				text-align: left;
				font-family: $font-family-base;
		    display: inline;
		  }
		}
	}

	.container {
		max-width: 1024px;
	}
}

.lineart-howitworks {
	// background-image: url("/assets/images/howitworkspanel.png");
	background-image: url("/assets/images/lineart/howworksleft.png"),
		url("/assets/images/lineart/howworksright.png");
	background-position: left, right;
	background-repeat: no-repeat;

	@include sm {
		background-image: url("/assets/images/lineart/howworksleft.png"),
			url("/assets/images/lineart/howworksright.png"),
			url("/assets/images/lineart/howworkstop.png");
		background-position: left, right, center top;
	}
}

.lineart-howareyoufeeling {
	background-image: url("/assets/images/lineart/feelingleft.png"),
		url("/assets/images/lineart/feelingright.png");
	background-position: left, right;
	background-repeat: no-repeat;

	@include sm {
		background-image: url("/assets/images/lineart/feelingleft.png"),
			url("/assets/images/lineart/feelingright.png"),
			url("/assets/images/lineart/feelingtop.png");
		background-position: left, right, center top;
	}
}

.lineart-connectwithus {
	background-image: url("/assets/images/lineart/connectleft.png"),
		url("/assets/images/lineart/connectright.png");
	background-position: left, right;
	background-repeat: no-repeat;

	@include sm {
		background-image: url("/assets/images/lineart/connectleft.png"),
			url("/assets/images/lineart/connectright.png"),
			url("/assets/images/lineart/connecttop.png");
		background-position: left, right, center top;
	}
}

