	// 	@include sm {
	// 		grid-template-rows: repeat(1, minmax(250px, 320px));
	// 	}
	// }

	&-col--2 {
		grid-template-columns: repeat(1, minmax(250px, 1fr));

		@include sm {
			grid-template-columns: repeat(2, minmax(250px, 1fr));
		}

		.masonry-content {
			display: flex;
			flex-direction: column;
			// justify-content: space-around;
			align-items: center;
			// height: 100%;
      flex-grow: 1;

			&--img {
				max-width: 34%;
			}

			.icons {
				display: flex;
				flex-direction: row;
				width: 100%;
				justify-content: space-evenly;

				@include sm {
					width: 80%;
				}

				a:hover {
					opacity: 0.5;
					-webkit-transition: opacity 0.3s ease;
					-o-transition: opacity 0.3s ease;
					transition: opacity 0.3s ease;
				}
			}

			&--align-left {
				align-items: flex-start;
				justify-content: space-evenly;

				h2 {
					text-align: start;
				}
				@media (max-width: $screen-xs-max) {
					align-items: center;
					h2 {
						text-align: center;
					}
				}
			}

			p {
				// font-weight: 100;
				text-align: center;
			}

			h2 {
				text-align: center;
			}

			.icons {
				img {
					width: 50px;
					height: 50px;
					border-radius: 50%;
				}
			}
		}
	}

	&-item {
		display: flex;
		flex-direction: column;
		justify-content: center;
		border-radius: 5px;
		margin: 12px 0;
		width: 100%;
		box-sizing: border-box;

		// margin: 0 50px;

    @include xs {
      width:100%;
			margin: 12px 0;
		}
		//
    @include sm {
      // width:50%;
			width: calc(50% - 24px);
			margin: 12px 12px;
		}
		//
		@include md {
      // width: 33%;
			width: calc(33% - 24px);
			margin: 12px 12px;
    }
		//
    // @include lg {
    //   width:320px;
    // }

		&--span2 {
			grid-column-end: span 2;
		}

		&--padding {
			padding: 20px 0 20px;
		}

		&--extra-space {
			margin: 20px 0;
		}

		&--dark-purple {
			background-color: $dark-purple;
			& .masonry-content {
				h3,
				h2,
				p,
				cite,
				a,
				.read-more,
				.watch-video {
					color: #fff;
				}

				.read-more::after {
					@include read-more-css-version(#fff);
				}
			}
		}

		&--purple {
			background-color: $brand-purple;

			& .masonry-content {
				h3,
				h2,
				p,
				cite,
				a,
				.read-more,
				.watch-video {
					color: #fff;
				}
				.read-more::after {
					@include read-more-css-version(#fff);
				}
			}
		}

		&--yellow {
			background-color: $brand-yellow;

      blockquote h3{
        &:first-of-type::before {
          content: url("/assets/images/66--yellow.svg");
        }
        &:last-of-type::after {
          content: url("/assets/images/99--yellow.svg");
        }
      }

			& .masonry-content {
				h3,
				h2,
				p,
				cite,
				a,
				.read-more,
				.watch-video {
					color: $black;
				}
				.read-more::after {
					@include read-more-css-version($black);
				}
			}
		}

		&--blue {
			background-color: $brand-blue;

      blockquote h3{
        &:first-of-type::before {
          content: url("/assets/images/66--blue.svg");
        }
        &:last-of-type::after {
        	content: url("/assets/images/99--blue.svg");
        }
      }

			& .masonry-content {
				h3,
				h2,
				p,
				cite,
				a,
				.read-more,
				.watch-video {
					color: $black;
				}
				.read-more::after {
					@include read-more-css-version($black);
				}
			}


		}

		&--green {
			background-color: $system-green;

      blockquote h3{
        &:first-of-type::before {
          content: url("/assets/images/66--green.svg");
        }
        &:last-of-type::after {
        	content: url("/assets/images/99--green.svg");
        }
      }


			& .masonry-content {
				h3,
				h2,
				p,
				cite,
				a,
				.read-more,
				.watch-video {
					color: $black;
				}
				.read-more::after {
					@include read-more-css-version($black);
				}
			}
		}

		&--white {
			background-color: $off-white;

			& .masonry-content {
				h3,
				p,
				cite,
				a,
				.read-more,
				.watch-video {
					color: $black;
				}
				.read-more::after {
					@include read-more-css-version($black);
				}
			}
		}

		&--light-purple {
			background-color: $light-purple;

      blockquote h3{
        &:first-of-type::before {
          content: url("/assets/images/66--light-purple.svg");
        }
        &:last-of-type::after {
          content: url("/assets/images/99--light-purple.svg");
        }
      }

			& .masonry-content {
				h3,
				h2,
				p,
				cite,
				a,
				.read-more,
				.watch-video {
					color: $black;
				}
				.read-more::after {
					@include read-more-css-version($black);
				}
			}
		}
	}
	&-content {
		padding: 0 20px;
		@include sm {
			padding: 0 25px;
		}
		// font-size: $font-size-medium;
		// font-weight: 100;
		// width: 100%;
		// height: 100%;

		h3 {
			color: #009096;
			font-style: italic;
			text-align: left;
		}

		p {
			font-size: 15px;
		}

		cite {
			font-size: $font-size-medium;
		}

		a {
			width: max-content;
		}

		.read-more,
		.watch-video {
			font-size: $font-size-base;
			@include sm {
				font-size: $font-size-medium;
			}

		}
	}
}

#testimonials blockquote h3 {
	margin-bottom: 0.5em;
}
