/* montserrat-regular - latin-ext_latin */
@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  src: url("/assets/fonts/montserrat-v14-latin-ext_latin-regular.eot");
  /* IE9 Compat Modes */
  src:local('Montserrat Regular'), local('Montserrat-Regular'), url('/assets/fonts/montserrat-v14-latin-ext_latin-regular.eot?#iefix') format('embedded-opentype'),
  /* IE6-IE8 */
  url('/assets/fonts/montserrat-v14-latin-ext_latin-regular.woff2') format('woff2'),
  /* Super Modern Browsers */
  url('/assets/fonts/montserrat-v14-latin-ext_latin-regular.woff') format('woff'),
  /* Modern Browsers */
  url('/assets/fonts/montserrat-v14-latin-ext_latin-regular.ttf') format('truetype'),
  /* Safari, Android, iOS */
  url( '/assets/fonts/montserrat-v14-latin-ext_latin-regular.svg#Montserrat') format('svg');
  /* Legacy iOS */
}
