// _variables
@import "variables";

// button settings
$padding-v: 8px;
$padding-h: 20px;
$button-radius: 30px;
$border-size: 3px;

@mixin button-settings {
	display: inline-block;
	position: relative;
	font-size: $font-size-base;
	padding: $padding-v $padding-h;
	border-radius: $button-radius;
	font-weight: $font-semi-bold;
}

// button mixins
@mixin button($color, $background, $border) {
	@include button-settings;
	cursor: pointer;

	&,
	&:visited {
		background-color: $background;
		border: $border-size solid $border;
		color: $color;
		text-decoration: none;
	}

	&:hover,
	&:focus,
	&:active {
		background-color: mix(#000, $background, 20%);
		border-color: mix(#000, $border, 20%);
		color: $color;
		text-decoration: none;
	}
}

@mixin button-wire($color, $hover-color, $border) {
	@include button-settings;

	&,
	&:visited {
		background-color: transparent;
		border: $border-size solid $border;
		color: $color;
		text-decoration: none;
	}

	&:hover,
	&:focus,
	&:active {
		background-color: $border;
		border-color: $border;
		color: $hover-color;
		text-decoration: none;
	}
}

// make container
@mixin make-container {
	display: block;
	margin-left: auto;
	margin-right: auto;
	padding-left: 15px;
	padding-right: 15px;

	&::before,
	&::after {
		content: "";
		display: table;
	}

	&::after {
		clear: both;
	}

	@include sm {
		width: 750px;
	}

	@include md {
		width: 970px;
	}

	@include lg {
		width: 1170px;
	}
}

// column helpers
@mixin column-center {
	float: none !important;
	margin-left: auto;
	margin-right: auto;
}

@mixin column-center-xs {
	float: none !important;
	margin-left: auto;
	margin-right: auto;

	@include sm {
		float: left !important;
		margin-left: 0;
		margin-right: 0;
	}
}

// text helpers
@mixin text-center-sm {
	text-align: center;

	@include md {
		text-align: inherit;
	}
}

@mixin text-center-xs {
	text-align: center;

	@include sm {
		text-align: inherit;
	}
}

@mixin text-center-xxs {
	@include xxs {
		text-align: center;
	}
}

/// block grid ///
// grid container
@mixin make-block-grid-container {
	@include clearfix;
	margin-left: ($grid-gutter-width / -2);
	margin-right: ($grid-gutter-width / -2);

	> * {
		margin-bottom: $grid-gutter-width;
		min-height: 1px;
		padding-left: ($grid-gutter-width / 2);
		padding-right: ($grid-gutter-width / 2);
		position: relative;
	}
}

// create the grids
@mixin create-grid($blocks) {
	float: left;
	width: percentage(1 / $blocks);

	&:nth-of-type(1n) {
		clear: none;
	}

	&:nth-of-type(#{$blocks}n + 1) {
		clear: both;
	}
}

// individual block grids
@mixin make-xxs-block-grid($blocks) {
	@include xxs {
		@include create-grid($blocks);
	}
}

@mixin make-xs-block-grid($blocks) {
	@include xs {
		@include create-grid($blocks);
	}
}

@mixin make-sm-block-grid($blocks) {
	@include sm {
		@include create-grid($blocks);
	}
}

@mixin make-md-block-grid($blocks) {
	@include md {
		@include create-grid($blocks);
	}
}

@mixin make-lg-block-grid($blocks) {
	@include lg {
		@include create-grid($blocks);
	}
}

// center block grid
@mixin center-block-grid {
	text-align: center;
	font-size: 0; // removes gaps between items

	> * {
		float: none;
		display: inline-block;
		vertical-align: top;
		font-size: initial;
	}
}

// block-grid shorthand mixin
// usage:	@include make-block-grid(1,2,3,4,6)
@mixin make-block-grid($cols-xxs, $cols-xs, $cols-sm, $cols-md, $cols-lg) {
	@include make-block-grid-container();

	> * {
		@include make-xxs-block-grid($cols-xxs);
		@include make-xs-block-grid($cols-xs);
		@include make-sm-block-grid($cols-sm);
		@include make-md-block-grid($cols-md);
		@include make-lg-block-grid($cols-lg);
	}
}

/// media queries ///
// xs >= 480px
@mixin xs {
	@media (min-width: $screen-xs-min) {
		@content;
	}
}

// sm >= 768px
@mixin sm {
	@media (min-width: $screen-sm-min) {
		@content;
	}
}

// md >= 992px
@mixin md {
	@media (min-width: $screen-md-min) {
		@content;
	}
}

// lg >= 1200px
@mixin lg {
	@media (min-width: $screen-lg-min) {
		@content;
	}
}

// xl >= 1440px
@mixin xl {
	@media (min-width: 1440px) {
		@content;
	}
}

// hackity hack
// xxs < 480px
@mixin xxs {
	@media (max-width: ($screen-xs-min) - 1px) {
		@content;
	}
}

/// extra mixins ///

// box sizing
@mixin box-sizing($boxmodel) {
	-webkit-box-sizing: $boxmodel;
	-moz-box-sizing: $boxmodel;
	box-sizing: $boxmodel;
}

// box-shadow
@mixin box-shadow($shadow...) {
	-webkit-box-shadow: $shadow; // iOS <4.3 & Android <4.1
	box-shadow: $shadow;
}

// clearfix
@mixin clearfix() {
	&:before,
	&:after {
		content: " ";
		display: table;
	}
	&:after {
		clear: both;
	}
}

// transitions
@mixin transition($transition...) {
	-webkit-transition: $transition;
	-o-transition: $transition;
	transition: $transition;
}
@mixin transition-property($transition-property...) {
	-webkit-transition-property: $transition-property;
	transition-property: $transition-property;
}
@mixin transition-delay($transition-delay) {
	-webkit-transition-delay: $transition-delay;
	transition-delay: $transition-delay;
}
@mixin transition-duration($transition-duration...) {
	-webkit-transition-duration: $transition-duration;
	transition-duration: $transition-duration;
}
@mixin transition-timing-function($timing-function) {
	-webkit-transition-timing-function: $timing-function;
	transition-timing-function: $timing-function;
}
@mixin transition-transform($transition...) {
	-webkit-transition: -webkit-transform $transition;
	-moz-transition: -moz-transform $transition;
	-o-transition: -o-transform $transition;
	transition: transform $transition;
}

//background colours

@mixin bg-colour() {
	&--yellow {
		background-color: $brand-yellow;
	}
	&--pink {
		background-color: $brand-pink;
	}
	&--blue {
		background-color: $brand-blue;
	}
	&--pale-purple {
		background-color: $brand-logo;
	}
	&--teal {
		background-color: $brand-teal;
	}
	&--purple {
		background-color: $brand-purple;
	}
	&--light-purple {
		background-color: $light-purple;
	}
	&--light-blue {
		background-color: $light-blue;
	}
	&--dark-purple {
		background-color: $dark-purple;
	}
}

$colourList: (
	white: $white,
	black: $black,
	off-white: $off-white,
	purple: $hero-purple,
	light-purple: $light-purple,
	soft-purple: #c6d3fd,
	dark-purple: $dark-purple,
	purple-logo-text: $brand-logo,
	light-blue: $light-blue,
	blue: $brand-blue,
	cyan: #8ff5f5,
	pink: $brand-pink,
	yellow: $brand-yellow,
	teal: $brand-teal,
	light-teal: $light-teal,
);

// options attribute for vapid directive (I have tried to group colours by likeness
// options='white, black, white, off-white, purple, light-purple, soft-purple, purple-logo-text, dark-purple, blue, light-blue, cyan, pink, yellow, teal'

$colourListNames: map-keys($colourList);

@mixin hero-gradients() {
	@each $label in $colourListNames {
		@each $label2 in $colourListNames {
			.hero-gradient--#{"" + $label}--#{"" + $label2} {
				background: linear-gradient(
					177deg,
					map-get($colourList, $label),
					map-get($colourList, $label2)
				);
				p{
					@if $label == purple{
						color: $white;
					}
					@if $label2 == purple{
						color: $white;
					}
				}
			}
		}
	}
}

@mixin read-more-css-version($border-color: black) {
	padding-left: 8px;
	border: solid $border-color;
	border-width: 0 3px 3px 0;
	display: inline-block;
	padding: 3px;
	content: "";
	transform: rotate(-45deg);
	margin-left: 5px;
	border-radius: 0 2px 2px 2px;
}

@mixin back-tick-css-version($border-color: black) {
	padding-left: 8px;
	border: solid $border-color;
	border-width: 0 3px 3px 0;
	display: inline-block;
	padding: 3px;
	content: "";
	transform: rotate(135deg);
	margin-right: 5px;
	border-radius: 2px 2px 2px 2px;
}

.read-more {
	display: inline-block;
	font-size: $font-size-base;
	font-weight: 600;

	&::after {
		// content: url("/assets/images/arrow-right.svg");
		// padding-left: 8px;
		@include read-more-css-version($brand-purple);
	}
	// TODO: make keyboard focus clearer
}
@mixin watch-video-icon() {
	display: inline-block;
	vertical-align: middle;
	position: relative;
	width: 26px;
	height: 26px;
	background: $brand-pink;
	border-radius: 50%;
	transform: rotate(90deg);
	margin-left: 5px;
	margin-bottom: 3px;
	&:after {
		position: absolute;
		content: "";
		top: 52%;
		left: 50%;
		border: 7px solid transparent;
		border-bottom: 12px solid #f4f8ff;
		transform: translateX(-50%) translateY(-85%);
		border-radius: 2px;
	}
}
